.calibrate {
    font-size:22px;
}

.calibrate .controls {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    
}

.calibrate .controls > div {
    width:40%;
}

.calibrate .joystick {
    width:100px;
    height:100px;
    background-size:100% 100%;
    -ms-interpolation-mode: nearest-neighbor;
        image-rendering: -webkit-optimize-contrast;
        image-rendering: -moz-crisp-edges;
        image-rendering: -o-pixelated;
        image-rendering: pixelated;
    

}

.calibrate .joystick.blue {
    background-image:url("/calibration/joystick-blue_idle.png");
}

.calibrate .joystick.blue.Right {
    background-image:url("/calibration/joystick-blue_right.png");
}

.calibrate .joystick.blue.Left {
    background-image:url("/calibration/joystick-blue_left.png");
}

.calibrate .joystick.red {
    background-image:url("/calibration/joystick-red_idle.png");
}

.calibrate .joystick.red.Right {
    background-image:url("/calibration/joystick-red_right.png");
}

.calibrate .joystick.red.Left {
    background-image:url("/calibration/joystick-red_left.png");
}

.calibrate .joystick.green {
    background-image:url("/calibration/joystick-green_idle.png");
}

.calibrate .joystick.green.Right {
    background-image:url("/calibration/joystick-green_right.png");
}

.calibrate .joystick.green.Left {
    background-image:url("/calibration/joystick-green_left.png");
}

.calibrate .joystick.yellow {
    background-image:url("/calibration/joystick-yellow_idle.png");
}

.calibrate .joystick.yellow.Right {
    background-image:url("/calibration/joystick-yellow_right.png");
}

.calibrate .joystick.yellow.Left {
    background-image:url("/calibration/joystick-yellow_left.png");
}

.calibrate .joystick.black {
    background-image:url("/calibration/joystick-black_idle.png");
}

.calibrate .joystick.black.Right {
    background-image:url("/calibration/joystick-black_right.png");
}

.calibrate .joystick.black.Left {
    background-image:url("/calibration/joystick-black_left.png");
}


.calibrate .button {
    background-image:url("/calibration/buttons.png");
    width:80px;
    height:80px;
    background-size:600% 200%;
    -ms-interpolation-mode: nearest-neighbor;
        image-rendering: -webkit-optimize-contrast;
        image-rendering: -moz-crisp-edges;
        image-rendering: -o-pixelated;
        image-rendering: pixelated;
}

.calibrate .button.start {
    background-position-x:-400%;
}

.calibrate .button.blue {
    background-position-x:0%;
}

.calibrate .button.red {
    background-position-x:-100%;
}

.calibrate .button.green {
    background-position-x:-200%;
}

.calibrate .button.yellow {
    background-position-x:-300%;
}

.calibrate .button.pedal {
    background-position-x:-500%;
}

.calibrate .highlight .button {
    background-position-y:-100%;
}

.calibrate .buttons {
    display:-ms-flexbox;
    display:flex;
}

.cache {
    display:none;
}


